@use 'base/mixins' as m;
@use 'base/variables' as v;

header{
    background-image: url('/build/img/Banner.jfif');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 650px;
    filter: brightness(1.5);

}

.header_barra{

    background-color: v.$azulOscuro;
    padding: 1rem 5rem 1rem 5rem;
    display: block;
    text-align: center;

    @include m.tablet {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 6rem;

        &.fijo{
            position: fixed;
            top:0;
            left: 0;
            background-color: v.$azulOscuro;
            width: 100%;
            z-index: 100000;
            -webkit-box-shadow: 0px 10px 13px -7px #000000, 19px 1px 13px 9px rgba(0,0,0,0.31); 
            box-shadow: 0px 10px 13px -7px #000000, 19px 1px 13px 9px rgba(0,0,0,0.31);
        }
    }

    .logo{
        a{
            color: v.$blanco;
            font-size: 4rem;
            font-weight: bold;
            span{
                font-weight: bold;
                color: v.$blanco;
            }
        }
    }

    .navegacion{
        color: v.$azulClaro;
        display: none;
        padding: .5rem;

        @include m.tablet {
            display: flex;
            gap: 2rem;
        }

        .seccion{
            color: v.$azulClaro;
            margin-bottom: 1rem;

            &.seleccionado{
                border-bottom: 2px solid v.$azulClaro;
            }
        }
    }

}

.header_contenido{
    position: relative;
    text-align: center;
    bottom: -50rem;
    
    @include m.tablet {
        text-align: left;
        top: 45rem;
        right: -5rem;
        bottom: 20px;
    }

    h2{
        color: v.$blanco;
        font-size: 2.8rem;
    }
}



