@use 'base/mixins' as m;
@use 'base/variables' as v;

.about{
    padding: 10rem 0 10rem 0;
    display: block;
    text-align: center;

    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 3rem;
    }
}

.resumen{
    h2{
        color: v.$azulOscuro;
    }
    p{
        color: v.$azulRey;
    }
    .boton{
        width: 30rem;
        margin: 0 auto;
    }
}

.imagen{
    padding: 8rem 0 4rem 0;
    @include m.tablet {
        padding: 4rem 0;
    }
    img{
        width: 30rem;
        border-radius: 50%;
        transform: rotate(90deg);
    }
}