@use 'base/mixins' as m;
@use 'base/variables' as v;

footer{
    background-color: v.$azulOscuro;
    text-align: center;
    padding-top: 6rem;
    padding-bottom: 2rem;
}

.redes{
    text-align: center;
    a{
        display: block;
        font-size: 3rem;
        margin-top: 1rem;
        color: v.$blanco;
        transition: all 0.5s ease;

        &:hover{
            color: v.$azulClaro;
            transform: translateX(10px);
        }
    }

    @include m.tablet {
        nav{
            display: flex;
            justify-content: center;
            gap: 2rem;

            a:hover{
                transform: translateY(-15px);
            }
       }

    }

    p{
        color: v.$blanco;
    }
}