@use 'base/mixins' as m;
@use 'base/variables' as v;

.contacto{
    background-color: v.$azulCielo;
    padding: 5rem 0;
}

.formulario{
    text-align: center;
    max-width: 45rem;
    h2{
        color: v.$azulOscuro;
    }

    .campo{
        display: flex;
        align-items: center;
        margin: 2rem 0;
        label{
            flex: 0 0 10rem;
            color: v.$azulOscuro
        }
        input,
        textarea{
            flex: 1;
            padding: 0.5rem;
            border: none;
            border-radius: .5rem;
        }
    }
}

.boton{
    text-align: center;
    @include m.boton(v.$azulOscuro, v.$azulClaro);
}