html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  body.barra_fija {
    margin-top: 6rem;
  }
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  line-height: 1.8;
}

.descripcion-pag {
  text-align: center;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

header {
  background-image: url("/build/img/Banner.jfif");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 650px;
  filter: brightness(1.5);
}

.header_barra {
  background-color: #0f2543;
  padding: 1rem 5rem 1rem 5rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .header_barra {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 6rem;
  }
  .header_barra.fijo {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #0f2543;
    width: 100%;
    z-index: 100000;
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 19px 1px 13px 9px rgba(0, 0, 0, 0.31);
    box-shadow: 0px 10px 13px -7px #000000, 19px 1px 13px 9px rgba(0, 0, 0, 0.31);
  }
}
.header_barra .logo a {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: bold;
}
.header_barra .logo a span {
  font-weight: bold;
  color: #FFFFFF;
}
.header_barra .navegacion {
  color: #0efff9;
  display: none;
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .header_barra .navegacion {
    display: flex;
    gap: 2rem;
  }
}
.header_barra .navegacion .seccion {
  color: #0efff9;
  margin-bottom: 1rem;
}
.header_barra .navegacion .seccion.seleccionado {
  border-bottom: 2px solid #0efff9;
}

.header_contenido {
  position: relative;
  text-align: center;
  bottom: -50rem;
}
@media (min-width: 768px) {
  .header_contenido {
    text-align: left;
    top: 45rem;
    right: -5rem;
    bottom: 20px;
  }
}
.header_contenido h2 {
  color: #FFFFFF;
  font-size: 2.8rem;
}

.about {
  padding: 10rem 0 10rem 0;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.resumen h2 {
  color: #0f2543;
}
.resumen p {
  color: #0867ac;
}
.resumen .boton {
  width: 30rem;
  margin: 0 auto;
}

.imagen {
  padding: 8rem 0 4rem 0;
}
@media (min-width: 768px) {
  .imagen {
    padding: 4rem 0;
  }
}
.imagen img {
  width: 30rem;
  border-radius: 50%;
  transform: rotate(90deg);
}

.skills {
  background-color: #0efff9;
  text-align: center;
  padding: 10rem 0 10rem 0;
}
.skills h2 {
  color: #0f2543;
}

.servicios {
  display: block;
}
@media (min-width: 768px) {
  .servicios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.servicio {
  font-size: 3rem;
  margin: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #0f2543;
  border-radius: 2rem;
  transition: all 0.4s ease;
}
.servicio:hover {
  color: #FFFFFF;
  background-color: #0f2543;
}
@media (min-width: 768px) {
  .servicio:hover {
    transform: translateX(-20px);
  }
}

.contacto {
  background-color: #08b2ff;
  padding: 5rem 0;
}

.formulario {
  text-align: center;
  max-width: 45rem;
}
.formulario h2 {
  color: #0f2543;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin: 2rem 0;
}
.formulario .campo label {
  flex: 0 0 10rem;
  color: #0f2543;
}
.formulario .campo input,
.formulario .campo textarea {
  flex: 1;
  padding: 0.5rem;
  border: none;
  border-radius: 0.5rem;
}

.boton {
  text-align: center;
  background-color: #0f2543;
  padding: 1.5rem 4rem;
  color: #0efff9;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #060e19;
  cursor: pointer;
}

footer {
  background-color: #0f2543;
  text-align: center;
  padding-top: 6rem;
  padding-bottom: 2rem;
}

.redes {
  text-align: center;
}
.redes a {
  display: block;
  font-size: 3rem;
  margin-top: 1rem;
  color: #FFFFFF;
  transition: all 0.5s ease;
}
.redes a:hover {
  color: #0efff9;
  transform: translateX(10px);
}
@media (min-width: 768px) {
  .redes nav {
    display: flex;
    justify-content: center;
    gap: 2rem;
  }
  .redes nav a:hover {
    transform: translateY(-15px);
  }
}
.redes p {
  color: #FFFFFF;
}